.footer {
  background-color: $black-night;
  border-radius: 30px 30px 0 0;

  &__message {
    @include onSmall {
      width: 362px;
    }

    @include onMiddle {
      margin-right: 98px;
    }

    @include onBig {
      margin-right: 227px;
    }
  }

  &__title {
    margin-bottom: 48px;
    color: $white;
    font-size: $mediumText;
    font-weight: $fontWeightText;
    line-height: 150%;

    @include onSmall {
      margin-bottom: 56px;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;

    &-button {
      width: 280px;
      margin-bottom: 80px;
      padding: 17px 0;
      font-size: 15px;
      font-weight: $fontWeightText;
      color: $white;
      border: $blue;
      border-radius: 8px;
      background-color: $blue;
      cursor: pointer;

      @include onSmall {
        width: 264px;
        margin-bottom: 120px;
      }
    }
  }

  &__input {
    height: 41px;
    outline: none;
    padding-bottom: 17px;
    margin-bottom: 40px;
    color: $white;
    background-color: $black-night;
    border-color: $white;
    border-width: 0 0 2px;
    transition: $allSpeedAnim;

    &:nth-child(3) {
      margin-bottom: 56px;

      @include onSmall {
        margin-bottom: 80px;
      }
    }

    &:focus {
      border-color: $blue;
    }

    &::placeholder {
      font-weight: $fontWeightText;
    }

    &:-webkit-autofill {
      -webkit-box-shadow: inset 0 0 0 50px $gray-lightMoon;
      -webkit-border-radius: 10px;
    }
  }

  &__content {
    padding: 72px 20px 80px;

    @include onSmall {
      padding: 180px 41px 80px;
    }

    @include onMiddle {
      padding: 180px 54px 80px;
    }

    @include onBig {
      padding: 180px 227px 80px;
    }

    @include onTV {
      padding: 180px 707px 80px;
    }
  }

  &__contacts {
    margin-bottom: 80px;
  }

  &__note {
    margin-bottom: 8px;
    color: $white;
    font-size: $smallText;
    font-weight: $fontWeightTitle;
    letter-spacing: 3px;
    text-transform: uppercase;
    opacity: $opacity;

    &:nth-child(4) {
      margin-bottom: 16px;
    }
  }

  &__media {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 160px;

    & > a:hover {
      color: $blue;
    }
  }

  &__about-us {
    display: flex;
    flex-direction: column;
  }

  &__text {
    margin-bottom: 40px;
    color: $white;
    font-size: 24px;
    font-weight: $fontWeightText;
    line-height: 150%;

    &:hover {
      opacity: $opacity;
      transition: $allSpeedAnim;
    }
  }

  &__link {
    color: $white;
    font-size: $smallText;
    font-weight: $fontWeightTitle;
    line-height: 150%;
    letter-spacing: 3px;
    text-transform: uppercase;

    &:hover {
      opacity: $opacity;
      transition: $allSpeedAnim;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;

    @include onMiddle {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      height: 28px;
      margin-left: 40px;
    }

    @include onBig {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      height: 28px;
      margin-left: 0;
    }
  }

  &__page {
    width: 46px;
    height: 28px;
    margin-bottom: 40px;

    @include onMiddle {
      margin-bottom: 0;

      &:hover {
        transform: scale(1.05);
      }
    }
  }

  &__item {
    margin-bottom: 24px;
    height: 20px;

    &:last-child {
      margin: 0;
    }

    @include onSmall {
      margin-bottom: 0;
      margin-right: 40px;
    }

    @include onBig {
      margin-bottom: 0;
      margin-right: 64px;
    }
  }

  &__list {
    @include onSmall {
      display: flex;
      height: 20px;
    }

    @include onMiddle {
      display: flex;
      justify-content: center;
      width: 72%;
      height: 20px;
    }
  }
}

.textarea {
  resize: none;

  &::-webkit-scrollbar {
    width: 10px;
  }

  &::-webkit-scrollbar-track {
    background-color: $gray;
    border-radius: 30px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $gray-lightMoon;
    border-radius: 30px;
  }

  &::-webkit-scrollbar-thumb:hover {
    background-color: $blue;
  }
}

.form__title {
  margin-bottom: 48px;
  color: $white;
  font-size: $mediumText;
  font-weight: $fontWeightText;
  line-height: 130%;

  @include onSmall {
    margin-bottom: 56px;
  }
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;

  @include onMiddle {
    display: flex;
    flex-direction: row;
  }

  @include onBig {
    display: flex;
    justify-content: space-between;
    width: 1024px;
  }
}

.button:hover {
  transition: $allSpeedAnim;
  transform: scale(1.05);
}

.footer__media-facebook {
  background-image: url(../images/logo/facebook.svg);
  @extend %mediaMarkSettings;

  &:hover {
    background-image: url(../images/logo/facebook__blue.svg);
  }
}

.footer__media-twitter {
  background-image: url(../images/logo/twitter.svg);
  @extend %mediaMarkSettings;

  &:hover {
    background-image: url(../images/logo/twitter__blue.svg);
  }
}

.footer__media-instagram {
  background-image: url(../images/logo/instagram.svg);
  @extend %mediaMarkSettings;

  &:hover {
    background-image: url(../images/logo/instagram__blue.svg);
  }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  -webkit-text-fill-color: $gray-lightMoon;
  transition: background-color 5000s ease-in-out 0s;
  -webkit-box-shadow: inset 0 0 0 50px $gray-lightMoon;
  -webkit-border-radius: 10px;
}
